import React from 'react';
import "../../../styles/accont.scss";
import { Link } from 'react-router-dom';
import { Toast } from 'antd-mobile';
class Accont extends React.Component {
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {
      shoop: JSON.parse(window.localStorage.getItem('detailCarts')),
      qty:0,
      pic:'',
      allCount:0
    }
  }
  componentDidMount() {
    let kong = this.state.shoop;
    let qty = 0;
    let allCount = 0;
    for (let i = 0; i < kong.length; i++) {
        qty += kong[i].nums;
        allCount += kong[i].nums * kong[i].price;
    }
    this.setState({
      qty: qty,
      pic:kong[0].goodPic,
      allCount:allCount
    })
  }
  tj() {
    Toast.success('', 2);
  }
  render() {
    return (
      <div id="accont">
        <div className="atb">
          <Link to="/footer/cart"><i className="fa fa-chevron-left" aria-hidden="true"></i></Link>
          <span className="accont"></span>
          <span></span>
        </div>
        <div className="aCenters">
          <div className="pay">
            <div className="address">
              <span><i class="fa fa-map-marker" aria-hidden="true"></i>
                :
              </span>
              <span><i class="fa fa-angle-right" aria-hidden="true"></i></span>
            </div>
            <div className="time">
              <span ></span>
              <span><i class="fa fa-angle-right" aria-hidden="true"></i></span>
            </div>
            <div className="method">
              <span></span>
              <span className="zhifu"><i class="fa fa-angle-down" aria-hidden="true"></i></span>
            </div>
            <div className="zf">
              <span><i class="fa fa-check-circle-o" aria-hidden="true"></i></span>
              <img src="data:image/png;base64," />
              <span className="wy"></span>
              <span className="ying">,188</span>
            </div>
            <div className="fp">
              <span></span>
              <span className="no"></span>
              <span><i class="fa fa-angle-right" aria-hidden="true"></i></span>
            </div>
            <div className="know">
              <span>,</span><a></a>
            </div>
          </div>
        </div>
        <div className="show">

              <div>
                <img src={this.state.pic} /><span>{this.state.qty}</span>
              </div>
        </div>
        <div className="aFooter">
          <div className="aleft">
            <span></span>
            <p>{this.state.allCount}</p>
          </div>
          <button className="aright" onClick={this.tj.bind(this)}></button>
        </div>
      </div>
    )
  }



}
export default Accont;