import React from 'react';
import { connect } from 'react-redux';
import "../../../styles/car.scss"
import '../../../static/css/font-awesome.css';
import Swiper from 'swiper';
import {Link} from 'react-router-dom';
class Cart extends React.Component{
    constructor(props) {
        super(props);
        this.props = props;
        this.state = {
            sszt:true,//
            sooping:true,//
            xiugai:true,//
            zhuangTai:true,//
            jiage:0,//
            qty:0,//
            xiangPing:[],//
            Bbottom:[{ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "829592160",CommentCount: 3794 ,CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1802011449449456_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 27442 ,MaskImageUrl: null, Name: "(30)",PriceTag: "",PriceTags: null ,SalePrice: 10 ,SortIndex: null, Uri: "inner:///item/27442"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "872748680",CommentCount: 2901 ,CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1806291544022668_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 27204 ,MaskImageUrl: null, Name: "5",PriceTag: "",PriceTags: null ,SalePrice: 15 ,SortIndex: null, Uri: "inner:///item/27204"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "838776970",CommentCount: 9552 ,CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1712221341265132_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 27544 ,MaskImageUrl: null, Name: "/",PriceTag: "",PriceTags: null ,SalePrice: 49 ,SortIndex: null, Uri: "inner:///item/27544"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "1/2",Code: "801213733",CommentCount: 27075, CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1709111527344118_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 25501 ,MaskImageUrl: null, Name: "(-2)",PriceTag: "",PriceTags: null ,SalePrice: 15 ,SortIndex: null, Uri: "inner:///item/25501"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "881290013",CommentCount: 354, CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1809141910458126_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 30073 ,MaskImageUrl: null, Name: "",PriceTag: "",PriceTags: null ,SalePrice: 69 ,SortIndex: null, Uri: "inner:///item/30073"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "856203700",CommentCount: 3058 ,CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1801091817575460_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 25611 ,MaskImageUrl: null, Name: "()",PriceTag: "",PriceTags: null ,SalePrice: 19 ,SortIndex: null, Uri: "inner:///item/25611"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "825177531",CommentCount: 4146 ,CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1709291536099584_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 23992 ,MaskImageUrl: null, Name: "",PriceTag: "",PriceTags: null ,SalePrice: 9 ,SortIndex: null, Uri: "inner:///item/23992"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "866997430",CommentCount: 6416 ,CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1712292008232144_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 26847 ,MaskImageUrl: null, Name: "",PriceTag: "",PriceTags: null ,SalePrice: 19 ,SortIndex: null, Uri: "inner:///item/26847"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "878021090",CommentCount: 3275 ,CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1712190959351339_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 28002 ,MaskImageUrl: null, Name: "()",PriceTag: "",PriceTags: null ,SalePrice: 49 ,SortIndex: null, Uri: "inner:///item/28002"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "892726590",CommentCount: 21318, CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1612292034178130_d230x230.gif",IsEmpty: false ,IsPotter: false ,ItemInfoId: 19640 ,MaskImageUrl: null, Name: "()",PriceTag: "",PriceTags: null ,SalePrice: 9 ,SortIndex: null, Uri: "inner:///item/19640"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "817346171",CommentCount: 4381 ,CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1708251714357038_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 25399 ,MaskImageUrl: null, Name: "(2)",PriceTag: "",PriceTags: null ,SalePrice: 29 ,SortIndex: null, Uri: "inner:///item/25399"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "PP",Code: "858529131",CommentCount: 472, CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1809071705532416_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 29935 ,MaskImageUrl: null, Name: "",PriceTag: "",PriceTags: null ,SalePrice: 19 ,SortIndex: null, Uri: "inner:///item/29935"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "2412413697",CommentCount: 33964, CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1507221016189640_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 15518 ,MaskImageUrl: null, Name: "(30)",PriceTag: "",PriceTags: null ,SalePrice: 10 ,SortIndex: null, Uri: "inner:///item/15518"},
                    {ActivityPrice: 0 ,ActivityTag: null, Appeal: "",Code: "827127320",CommentCount: 26010, CountdownTag: null ,CountdownTime: 0, ImageUrl: "/upload/goods/D1704141723322525_d230x230.jpg",IsEmpty: false ,IsPotter: false ,ItemInfoId: 22170 ,MaskImageUrl: null, Name: "()",PriceTag: "",PriceTags: null ,SalePrice: 9 ,SortIndex: null, Uri: "inner:///item/22170"}

                    ],//
            guangGao:{}// 
        }
    }
    haoshu(){
        this.panduan();
        this.panduan1();
        this.panduan2();
    }
    componentDidMount(){
        if(window.localStorage.getItem('userId')){
            let haooo= JSON.parse(window.localStorage.getItem('detailCarts'));
            let xiangPing =[];
            if(haooo != null){
                for(let i=0;i<haooo.length;i++){
                    if(haooo[i].yhm==window.localStorage.getItem('userId')){
                        xiangPing.push(haooo[i]);
                    }
                }
            }
            let sszt=false;
            this.setState(
                Object.assign({}, { xiangPing }),
                () => this.haoshu()//
            )
            this.setState(
                Object.assign({}, { sszt })
            )
        }else{
            let sszt=true;
            this.setState(
                Object.assign({}, { sszt }),
                () => this.haoshu()//
            )
        }
        


        this.props.changeSele();
        //     window.localStorage.setItem('CarDiBu',JSON.stringify(res.data.RecommendItems));

        // if(JSON.parse(window.localStorage.getItem('CarDiBu'))){
        //     let Bbottom = JSON.parse(window.localStorage.getItem('CarDiBu'));
        //     //JSON[object] [object]

            
        //     this.setState(
        //         Object.assign({}, { Bbottom })
        //     )
        // }
        React.axios.get('http://marketing.lifevc.com/v1/getCart?si=qsSTiCma24QNrqm%2F79krpdZ0mpRbm96Kk%2BA6DNvjV0dM52vH6%2BlALMJGNPJRqqbkZaK3iA2i0hQ%3D&ck=71f002fe_224109620&regionId=15247&pm=8&o=http%3A%2F%2Fm.lifevc.com&NewCartVersion=true')
        .then((res)=>{
            let guangGao = res.data.Prompts[0];
            this.setState({
                guangGao:guangGao
            })
        })
        .catch((err)=>{
            console.log(err);
        })
    }
    componentDidUpdate(){
          // <!-- Initialize Swiper -->
        var swiper2 = new Swiper('#center', {
            slidesPerView: 3,
            spaceBetween: 30,
            pagination: {
                el: '.swiper-pagination',
                clickable: true,
            },
        });
    }
    xiuGai(){
        this.setState({
            xiugai:!this.state.xiugai
        })
    }
    //
    jian(index){
        let dalao;
        if(this.state.xiangPing[index].nums>1){
            dalao = this.state.xiangPing;
            dalao[index].nums=dalao[index].nums-1;
            this.setState({
                xiangPing:dalao
            })
        }
        window.localStorage.setItem('detailCarts',JSON.stringify(this.state.xiangPing));
        this.panduan1();
         React.axios.get("http://10.3.136.17:3001/goods/goodsAdd",{params:{name:dalao[index].name,yhm:dalao[index].yhm,ext:"jian"}})
        .then((res)=>{})
    }
    //
    jia(index){
        let dalao = this.state.xiangPing;
        dalao[index].nums=dalao[index].nums+1;
        this.setState({
            xiangPing:dalao
        })
        window.localStorage.setItem('detailCarts',JSON.stringify(this.state.xiangPing));
        this.panduan1();
        React.axios.get("http://10.3.136.17:3001/goods/goodsAdd",{params:{name:dalao[index].name,yhm:dalao[index].yhm,ext:"jia"}})
        .then((res)=>{})
    }
    //
    danshan(index){
        let dalao = this.state.xiangPing;
        dalao.splice(index,1);
        this.setState({
            xiangPing:dalao
        })
        window.localStorage.setItem('detailCarts',JSON.stringify(this.state.xiangPing));
        this.panduan();
        this.panduan1();
        this.panduan2();
        React.axios.get("http://10.3.136.17:3001/goods/goodsDell",{params:{name:dalao[index].name,yhm:dalao[index].yhm}})
        .then((res)=>{})
    }
    //
    pilianshan(){
        let dalao = this.state.xiangPing;
        for(let i=0;i<this.state.xiangPing.length;i++){
            if(this.state.xiangPing[i].status){
                dalao.splice(i,1);
                this.pilianshan();//
            }
        }
        this.setState({
            xiangPing:dalao
        })
        window.localStorage.setItem('detailCarts',JSON.stringify(this.state.xiangPing));
        this.panduan();
        this.panduan1();
        this.panduan2();
    }
    //
    quanXuan(){
        let Zt = [];
        for(let i=0;i<this.state.xiangPing.length;i++){
            if(this.state.xiangPing[i].status){
                Zt.push(this.state.xiangPing[i].status)
            }
        }
        if(this.state.xiangPing.length==Zt.length){
            let dalao = this.state.xiangPing;
            for(let i=0;i<this.state.xiangPing.length;i++){
                dalao[i].status=false;
            }
            this.setState({
                xiangPing:dalao,
                zhuangTai:false
            })
        }else if(this.state.xiangPing.length!=Zt.length){
            let dalao = this.state.xiangPing;
            for(let i=0;i<this.state.xiangPing.length;i++){
                dalao[i].status=true;
            }
            this.setState({
                xiangPing:dalao,
                zhuangTai:true
            })
        }
        this.panduan1();
    }
    //
    danxuan(index){
        let dalao = this.state.xiangPing;
        dalao[index].status=!this.state.xiangPing[index].status;
        this.setState({
            xiangPing:dalao,
            zhuangTai:true
        })
        this.panduan();
        this.panduan1();
    }
    //
    panduan(){
        let Zt = [];
        for(let i=0;i<this.state.xiangPing.length;i++){
            if(this.state.xiangPing[i].status){
                Zt.push(this.state.xiangPing[i].status)
            }
        }
        if(this.state.xiangPing.length==Zt.length){
            this.setState({
                zhuangTai:true
            })
        }else if(this.state.xiangPing.length!=Zt.length){
            this.setState({
                zhuangTai:false
            })
        }
    }
    //
    panduan1(){
        let qian=0;
        let shu =0;
        for(let i=0;i<this.state.xiangPing.length;i++){
            if(this.state.xiangPing[i].status){
                qian+=(this.state.xiangPing[i].nums-0)*(this.state.xiangPing[i].price-0);
                shu+=this.state.xiangPing[i].nums-0;
            }
        }
        this.setState({
            jiage:qian,
            qty:shu
        })
        this.props.changegoodsCount(shu);
    }
    //
    panduan2(){
        if(!window.localStorage.getItem('userId')){
            let sooping=false;
            this.setState(
                Object.assign({}, { sooping })
            )
        }
        if(this.state.xiangPing.length==0){
            let sooping=false;
            this.setState(
                Object.assign({}, { sooping })
            )
        }
    }
    back(){
        this.props.history.go(-1);
    }
    render(){
        return (
            <div className='cart'>
                <div className="Top">
                    <span onClick={this.back.bind(this)}><i className="fa fa-chevron-left" aria-hidden="true"></i></span>
                    <p className="header-title"></p>
                    <div>{
                        (()=>{
                            if(this.state.sooping){
                                return <div className="header-right" onClick={this.xiuGai.bind(this)}>
                                    <div className="i">
                                        <i className="fa fa-pencil-square-o" aria-hidden="true"></i>
                                    </div>

                                    <div>{
                                        (()=>{
                                            if(this.state.xiugai){
                                                return <div>
                                                    <div className="p"></div>
                                                </div>
                                            }
                                        })()
                                    }</div>
                                    <div>{
                                        (()=>{
                                            if(!this.state.xiugai){
                                                return <div>
                                                    <div className="p"></div>
                                                </div>
                                            }
                                        })()
                                    }</div>
                                    

                                </div>
                            }
                        })()
                    }</div>
                </div>

                <div>{
                    (()=>{
                        if(this.state.sszt){
                            return <div  className="login-wrap">
                                        <div className="login"><Link to="/footer/user/" className="logins"></Link>
                                            <span className="tips"></span>
                                        </div>
                                    </div>
                        }
                    })()
                }</div>
                <div>{
                    (()=>{
                        if(!this.state.sszt){
                            return <div  className="login-Kon">
                                    </div>
                        }
                    })()
                }</div>
                
                <div className="change">
                  <input type="text" maxLength="15" placeholder="" className="ipts"/>
                  <input type="button" value="" className="exch-btn"/>
                </div>
                <div>{
                    (()=>{
                        if(!this.state.sooping){
                            return <div>
                                <div className="carts-img">
                                    <img src="data:image/png;base64," alt="" />
                                    <p></p>
                                    <div className="carts-link">
                                        <Link to="/footer/home/index/">
                                            <input type="button" value="" className="carts-guan"></input>
                                        </Link>
                                    </div>
                                </div>

                                <div className="cart-bottom">
                                    <div className="cart-assem-title">
                                        <p>Rico.S</p>
                                    </div>

                                    <div className="swiper-container carts" id="center">
                                         <div className="swiper-wrapper">
                                            {
                                                 this.state.Bbottom.map((item,index)=>{
                                                    return <div className="swiper-slide" key={index} >
                                                        <Link to={'/detail/'+item.ItemInfoId} >
                                                            <img src={'http://i.lifevc.com'+item.ImageUrl}/>
                                                            <p>{item.Name}</p>
                                                            <p><span>{item.SalePrice}</span></p>
                                                        </Link>
                                                    </div>
                                                 })
                                            }

                                        </div>
                                    </div>

                                </div>

                            </div>
                        }
                    })()
                }</div>

                


                <div>{
                    (()=>{
                        if(this.state.sooping){
                            return <div className="cars-main">
                                <div className="cars-Ggao">
                                    <a href={this.state.guangGao.Url}>
                                        <span>{this.state.guangGao.Tag}</span>
                                        {this.state.guangGao.Text}
                                    </a>
                                </div>

                                <div className="Sping">{(() => {
                                    return this.state.xiangPing.map((item, index) => {
                                        return <div className="xSping" key={index}>
                                                    <div className="ip">
                                                        <input type="checkbox" checked={item.status} onClick={this.danxuan.bind(this,index)} />
                                                    </div> 
                                                    <Link className="ig" to={'/detail/'+item.goodId} >
                                                        <img src={item.goodPic} />
                                                    </Link>


                                                    {
                                                        (()=>{
                                                            if(this.state.xiugai){
                                                                return <div className="Mag">
                                                                    
                                                                    <p>
                                                                        <Link className="Mag-a" to={'/detail/'+item.goodId} >{item.name}</Link>
                                                                        <span>{item.price}</span>
                                                                    </p>
                                                                    <p><span></span><span>x {item.nums}</span></p>
                                                                </div>
                                                            }
                                                        })()
                                                    }

                                                    {
                                                        (()=>{
                                                            if(!this.state.xiugai){
                                                                return <div className="Mag"><p>
                                                                        <div className="iii" onClick={this.danshan.bind(this,index)}>
                                                                            <i className="fa fa-trash-o" aria-hidden="true"></i>
                                                                        </div></p>
                                                                    <p>
                                                                        <div className="quantity-editor">
                                                                            <span className="tag-act-wrap"></span>
                                                                            <span className="decrement" onClick={this.jian.bind(this,index)} >-</span>
                                                                            <span type="text" className="quantity-num">{item.nums}</span>
                                                                            <span className="increment" onClick={this.jia.bind(this,index)}>+</span>
                                                                        </div>
                                                                    </p>
                                                                </div>
                                                            }
                                                        })()
                                                    }


                                                </div>
                                    })

                                })()}

                                    
                                </div>




                                <div className="carts-sping-di">
                                    <span></span>
                                    <select className="form-control">
                                        <option value="1"></option>
                                        <option value="2"></option>
                                    </select>&nbsp;&nbsp;&nbsp;
                                    <span>69</span>
                                </div>


                                <div className="carts-quanxuan">
                                    <div className="ip">
                                        <input type="checkbox" checked={this.state.zhuangTai} onClick={this.quanXuan.bind(this)} />
                                    </div>
                                    <div className="carts-quanX">
                                        <span></span>
                                    </div>
                                    <div className="carts-Price">
                                        <p><span>{this.state.jiage}</span></p>
                                        <p className="catr-teS"><span>{this.state.jiage}</span>&nbsp;- <span>0</span></p>
                                    </div>

                                    {(()=>{
                                        if(this.state.xiugai){
                                            return <div className="carts-kuang1">
                                            <Link to="/Accont" >
                                                 (<span>{this.state.qty}</span>)
                                            </Link> 
                                            </div>
                                        }
                                    })()}
                                    {(()=>{
                                        if(!this.state.xiugai){
                                            return <div className="carts-kuang2" onClick={this.pilianshan.bind(this)}></div>
                                        }
                                    })()}
                                </div>


                            </div>
                        }
                    })()
                }</div>
            </div>
        )
    }
}
export default connect((state)=>{
    return state
},(dispatch=>{
    return {
        changeSele() {
            dispatch({
                type:"toggleGallery",
                sele:3
            })
        },
        changegoodsCount(counts){
            dispatch({
                type:"changegoodsCount",
                goodsCount:counts
            })
        }
    }
}))(Cart);